import React from 'react';
import { FloatingLabel, Form, InputGroup } from "react-bootstrap"
import { NotificationManager } from 'react-notifications'
import { FaSortDown } from "react-icons/fa"
import { connect } from 'react-redux'

import OrderPanel from "./OrderPanel"
import { getNumberOrder, sendOrder } from "../api"

import "../styles/Order.scss"

class Order extends React.Component{
	constructor(props){
		super(props)

		this.stateOrderPanel = {
			id: 0,
			name: "",
			size: "",
			count: 0,
			price: 0,
			allPrice: 0
		}

		this.state = {
			// Components state
			additional_info_active: false,

			numberOrder: 0,
			additional: {
				delivery: false,
				assembly: false,
				entering: false,
				assemblyAndEntering: false
			},
			orders: [this.stateOrderPanel],
			allPrice: 0, 
			validatation: []
		}

		// for Client
		this.client_name = React.createRef()
		this.client_phone = React.createRef()
		this.client_address = React.createRef()

		// for order
		this.siteName = React.createRef()
		this.week = React.createRef()
		this.dateDelivery = React.createRef()
		this.year = React.createRef()

		// additional info for prices 
		this.delivery_price = React.createRef()
		this.assembly_price = React.createRef()
		this.entering_price = React.createRef()
		this.assemblyAndEntering_price = React.createRef()
	}

	onClickAddOrder(e){
		this.setState({ orders: [...this.state.orders, this.stateOrderPanel] })
	}

	// events for components
	onClickAdditionalInfo(){
		this.setState({ additional_info_active: !this.state.additional_info_active })
	}

	onChangeDelivery(e){ this.setState({ additional: {...this.state.additional, delivery: e.target.checked } }) }
	onChangeAssembly(e){ this.setState({ additional: {...this.state.additional, assembly: e.target.checked } }) }
	onChangeEntering(e){ this.setState({ additional: {...this.state.additional, entering: e.target.checked } }) }
	onChangeAssemblyAndEntering(e){ this.setState({ additional: {...this.state.additional, assemblyAndEntering: e.target.checked } }) }

	//---------------------------------------------------------------------------
	//------------------------------ORDER EVENTS--------------------------------
	//---------------------------------------------------------------------------

	addOrderToArray(e, data){
		e.preventDefault()
		console.log(data)
		let currentStateOrders = this.state.orders
		currentStateOrders[data.id] = data
		this.setState({ orders: currentStateOrders, allPrice: currentStateOrders.reduce((prev, cur) => {return prev + cur.allPrice}, 0) })
	}

	deleteOrderFromArrayById(id){
		if(window.confirm("Вы уверены? Данные для этого заказа не сохранятся.")){
			let currentStateOrders = this.state.orders
			currentStateOrders.splice(id, 1)
			this.setState({ orders: currentStateOrders })
		}
		
	}

	// main method for sending order to server
	onSendThisOrder(e){
		e.preventDefault()
		if(this.state.validatation.length === 0){
			const clientData = {
				name: this.client_name.current.value,
				phone: this.client_phone.current.value,
				address: this.client_address.current.value,
			}
			const additionalData = {
				siteName: this.siteName.current.value,
				week: +this.week.current.value,
				dateDelivery: this.dateDelivery.current.value,
				id: this.state.numberOrder,
				year: +this.year.current.value,
	
				delivery: this.state.additional.delivery,
				assembly: this.state.additional.assembly,
				entering: this.state.additional.entering,
				assemblyAndEntering: this.state.additional.assemblyAndEntering,
	
				delivery_price: this.delivery_price.current.value,
				assembly_price: this.assembly_price.current.value,
				entering_price: this.entering_price.current.value,
				assemblyAndEntering_price: this.assemblyAndEntering_price.current.value,
			}
	
			sendOrder(clientData, additionalData, this.state.orders, (res) => {
				NotificationManager.success('Данные успешно отправлены', 'OK', 3000);
			}, (err) => NotificationManager.warning('Данные не отправились.', 'Ошибка', 3000))
		}
		else NotificationManager.error('Данные не корректны', 'Ошибка', 3000)
	}

	onPressWeekInput(e){
		if(e.key === "Enter"){
			e.preventDefault()
			getNumberOrder(this.week.current.value, this.year.current.value, (data) => {
				this.setState({ numberOrder: data.id })
			}, err => {console.log(err)})
		}
	}

	validate(e){
		const add = (_e) => this.setState({ validatation: [...this.state.validatation, _e.target.name] })
		const remove = (_e) => this.setState({ validatation: this.state.validatation.filter(item => _e.target.name !== item) })

		switch(e.target.name){
			case "name":
				!(	e.target.value.length > 3 || 
					e.target.value == "-" || 
					e.target.value.toLowerCase() == "нет"
				)? add(e): remove(e); return
			case "phone":
				!(e.target.value.length > 6)? add(e): remove(e); return
			case "address":
				!(	e.target.value.length > 3 || 
					e.target.value == "-" || 
					e.target.value.toLowerCase() == "нет"
				)? add(e): remove(e); return
			case "week":
				!(	+e.target.value >= 1 && !!Number(e.target.value))?
				add(e): remove(e); return
			
			case "date_deliver":
				!(	e.target.value.length > 3 || 
					e.target.value == "-" || 
					e.target.value.toLowerCase() == "нет"
				)? add(e): remove(e); return
			
			
			default:
				console.log("Case don't registration")
		}
	}

	render(){
		const FLabel = _props => {
			return (
				<p className={"FLabel_line" + (_props.mark? " mark_bold" : "")}>
					<span className="line_key_span">{_props._key}</span>
					<span className="line_value_span">{_props.value}</span>
				</p>
			)	
		}
		return(
			<Form className="Order_wrapper" action="javascript:0">
				<div className="Order">
					<h1>Формирование нового заказа</h1>
					<h2>Информация о клиенте</h2>
					<div className="top_panel">
						<div className="order_form_user">
							<FloatingLabel hasValidation controlId="floatingInput" label="ФИО клиента" className="mb-12">
									<Form.Control isInvalid={this.state.validatation.includes("name")} autoFocus
										ref={this.client_name} 
										name="name"
										type="text" 
										placeholder="Иванов Иван Иванович"
										onBlur={e => this.validate(e)}
										title="Впишите '-' или 'нет', чтобы не вписывать информацию"
									/>
							</FloatingLabel>

							<FloatingLabel hasValidation controlId="floatingInput" label="Телефон" className="mb-12">
								<Form.Control required isInvalid={this.state.validatation.includes("phone")}
										ref={this.client_phone} 
										name="phone"
										type="tel" 
										placeholder="8 123 456 78 90"
										onBlur={e => this.validate(e)}
									/>
							</FloatingLabel>

							<FloatingLabel hasValidation controlId="floatingInput" label="Адрес" className="mb-12">
								<Form.Control 
									isInvalid={this.state.validatation.includes("address")} 
									onBlur={e => this.validate(e)} 
									ref={this.client_address} 
									type="text" 
									name="address"
									title="Впишите '-' или 'нет', чтобы не вписывать информацию"
									placeholder="г. Муром, ул. Ленина, 19, кв. 9"/>
							</FloatingLabel>
						</div>
						<div className="order_form_additional">
							
							<Form.Select ref={this.siteName} aria-label="Default select example" className="select_site">
								<option>Выберете сайт</option>
								<option value="Krovati-spalni.ru">Krovati-spalni.ru</option>
								<option value="мебельизсосны.рф">мебельизсосны.рф</option>
								<option value="Liman-murom.ru">Liman-murom.ru</option>
							</Form.Select>
							<div className="within">
								<Form.Select ref={this.year} aria-label="year" className="select_year">
									<option>Год</option>
									<option value="2021" selected>2021</option>
									<option value="2022">2022</option>
								</Form.Select>
								<FloatingLabel controlId="floatingInput" label="№ недели" className="mb-12">
									<Form.Control 
										isInvalid={this.state.validatation.includes("week")} 
										onBlur={e => this.validate(e)}
										name="week"
										ref={this.week} 
										className="within_input" 
										type="number" 
										placeholder="33" 
										onKeyDown={this.onPressWeekInput.bind(this)} required/>
								</FloatingLabel>
								<FloatingLabel controlId="floatingInput" label="№ заказа" className="mb-12">
									<Form.Control 
										value={this.state.numberOrder} 
										className="within_input" 
										disabled 
										type="number" 
										title="Впишите № недели и нажмите на Enter"
										placeholder="34"/>
								</FloatingLabel>
							</div>

							<FloatingLabel hasValidation controlId="floatingInput" label="Дата доставки" className="mb-12">
								<Form.Control 
									isInvalid={this.state.validatation.includes("date_deliver")} 
									onBlur={e => this.validate(e)} 
									ref={this.dateDelivery} 
									type="text" 
									name="date_deliver"
									title="Впишите '-' или 'нет', чтобы не вписывать информацию"
									placeholder="34.4.2032"/>
							</FloatingLabel>
						</div>
						
					</div>
					<h2>Заказы</h2>
					{
						this.state.orders.map((order, i) => {
							return <OrderPanel id={i} deleteOrderFromArrayById={this.deleteOrderFromArrayById.bind(this)} addOrderToArray={this.addOrderToArray.bind(this)}/>
						})
					}

					
					<button onClick={this.onClickAddOrder.bind(this)} className="btn btn-primary button_add_order">Добавить заказ</button>
					<p className="dfrowaic" style={{cursor: "pointer", marginTop: "10px"}} onClick={this.onClickAdditionalInfo.bind(this)}>
						Дополнительно {!this.state.additional_info_active? <FaSortDown />: <>:</>}
					</p>

					<div className="additional_info" style={{display: this.state.additional_info_active? "block": "none"}}>
						<Form.Check type="checkbox" id={`delivery`}
							// label={<span>Доставка<span className="price_grey"><input ref={this.delivery_price} type="number" pattern="[0-9]{5}"/> руб</span></span>}
							label={
								<>
									<span>Доставка</span>
									<span className="price_grey">
										<input className="form-control" min={0} max={1000000} ref={this.delivery_price} type="number"/> 
									руб</span>
								</>
							}
							onChange={this.onChangeDelivery.bind(this)}/>

						<Form.Check type="checkbox" id={`assembly`} 
							// label={<span>Сборка<span className="price_grey"> <input ref={this.assembly_price} type="number"/> руб</span></span>}
							label={
								<>
									<span>Сборка</span>
									<span className="price_grey">
										<input className="form-control" min={0} max={1000000} ref={this.assembly_price} type="number"/> 
									руб</span>
								</>
							}
							onChange={this.onChangeAssembly.bind(this)}/>

						<Form.Check type="checkbox" id={`entering`}
							// label={<span>Занос<span className="price_grey"><input ref={this.entering_price} type="number"/> руб</span></span>}
							label={
								<>
									<span>Занос</span>
									<span className="price_grey">
										<input className="form-control" min={0} max={1000000} ref={this.entering_price} type="number"/> 
									руб</span>
								</>
							}
							onChange={this.onChangeEntering.bind(this)}/>

						<Form.Check type="checkbox" id={`assemblyAndEntering`}
							label={
								<>
									<span>Сбор/Занос</span>
									<span className="price_grey">
										<input className="form-control" min={0} max={1000000} ref={this.assemblyAndEntering_price} type="number"/> 
									руб</span>
								</>
							}
							onChange={this.onChangeAssemblyAndEntering.bind(this)}/>
					</div>
					<hr />

					{
						this.state.orders.map(order => {
							return <FLabel _key={`Заказ #${order.id + 1}`} value={`${order.allPrice} руб`} />
						})
					}
					
					<FLabel mark={true} _key="Итог" value={`${this.state.allPrice} руб`} />
					<button className="btn btn-success button_send_order" onClick={this.onSendThisOrder.bind(this)}>Отправить заказ</button>
				</div>
			</Form>
		)
	}
}

const mapStateToProps = (state) => ({
	prices: state.prices
})

export default connect(mapStateToProps, null)(Order)