import React from "react"

import { getUsers, sendDataUsersForSave } from "../api"
import { Type }  from 'react-bootstrap-table2-editor'

import UniversalTable from "./UniversalTable"

const ERole = [
	<span className="_mark mark-admin">Администратор</span>, 
	<span className="_mark mark-moderator">Модератор</span>, 
	<span className="_mark mark-editor">Редактор</span>, 
	"Пользователь"]

class Users extends React.Component{
	constructor(props){
		super(props)
		this.columns = [
			{
				text: 'ID',
				dataField: 'id',
				editable: false
			},
			{
				text: 'ФИО',
				dataField: 'name'
			},
			{
				text: 'Логин',
				dataField: 'login',
				editable: false
			},
			{
				text: 'Роль',
				dataField: 'role',
				formatter: (cell, row) => { return ERole[row.role] },
				editor: {
					type: Type.SELECT,
					options: [{
					  value: "0",
					  label: 'Администратор'
					}, {
					  value: "1",
					  label: 'Модератор'
					}, {
					  value: '2',
					  label: 'Редактор'
					}, {
					  value: '3',
					  label: 'Пользователь'
					}]
				 }
			},
			{
				text: 'Активен',
				dataField: 'active',
				formatter: (cell, row) => { 
					return row.active == 1? 
						<span className="_mark mark-moderator">Активен</span>:
						<span className="_mark mark-admin">Не активен</span>
				},
				editor: {
					type: Type.SELECT,
					options: [{
					  value: '0',
					  label: 'Не активен'
					}, {
					  value: '1',
					  label: 'Активен'
					}]
				 }
			}
		]
	}
	

	render(){
		return(
			<div className="ManageUsers">
				{/* <h1 style={{textAlign: "center", fontFamily: "RobotoBold", fontSize: "1.5em"}}>Управление пользователями</h1> */}
				
				<UniversalTable
					className="users"
					title="Управление пользователями"
					columns={this.columns}

					getDataFromAPI={getUsers}
					saveDataToAPI={sendDataUsersForSave}

					optPagination={true}
					enableNewRow={false}
				/>

				
			</div>
		)
	}
}


export default Users