import React from "react"
import { NotificationContainer } from "react-notifications"
import {
  HashRouter as Router,
  Switch,
  Route
} from "react-router-dom"

// Components
import SideBar from "./components/SideBar"
import Header from "./components/Header"
import Authorization from "./hoc/authorization"

// redux and API
import { auth } from "./api"
import { addUser } from "./redux/actions/users.action"
import { authUser } from "./redux/actions/app.action"
import { routes } from "./routes"
import { connect } from "react-redux"

// styles
import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-notifications/lib/notifications.css';
import './styles/App.scss';
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';



class App extends React.Component{
	constructor(props){
		super(props)

		this.state = {}
	}

	componentDidMount(){
		// Authorization and set token
		if(!!localStorage.getItem("token")){
			auth(localStorage.getItem("token"), res => {
				this.setState({ auth: true, userActive: res.active })
				this.props.addUser(res.login, res.name, res.role, res.active)
				this.props.authUser(!res.error)
				localStorage.setItem("token", res.jwt)
			}, err => this.setState({ auth: false, userActive: 0 }) )
		}
		else{
			this.props.authUser(false)
			this.setState({ auth: false, userActive: 0 })
		}
	}

	render(){
		return (
			<Router>
				<React.Fragment>
					<Header />
					<SideBar routes={routes.filter(route => route.isNavBar || (route.isPrivate !== undefined && !route.isPrivate))} />
					<NotificationContainer />

					<main>
						<Switch>
							{routes.map(route => {
								const component = 
									route.isPrivate && (!this.props.auth || !this.props.user.active || route.role_user < this.props.user.role)?
										Authorization(route.component):
										route.component
								return (
								<Route
									key={route.path}
									path={route.path}
									component={component}
									exact={true}
								/>
								)
							})}
						</Switch>
					</main>
				</React.Fragment>
			</Router>
		)
	}
}


const mapDispatchToProps = dispatch => ({
	addUser: (login,  name, role, active) => dispatch(addUser(login,  name, role, active)),
	authUser: (auth) => dispatch(authUser(auth))
})

const mapStateToProps = state => ({
	user: state.user,
	auth: state.app.authorization
})


export default connect(mapStateToProps, mapDispatchToProps)(App)