import { APP } from "../types"

export const authUser = (auth) => ({
	type: APP.AUTH,
	payload: auth
})

export const logoutUser = () => ({
	type: APP.LOGOUT,
	payload: false
})


export const addInvoiceChange = change => ({
	type: APP.INVOICE_ADD_CHANGE,
	payload: change
})

export const saveInvoiceChanges = (invoiceData) => ({
	type: APP.INVOICE_SAVE_CHANGES,
	payload: invoiceData
})