import React from "react"
import { NotificationManager } from "react-notifications"
import BootstrapTable from 'react-bootstrap-table-next';
import cellEditFactory from 'react-bootstrap-table2-editor'
import paginationFactory from 'react-bootstrap-table2-paginator'



export default class UniversalTable extends React.Component{
	constructor(props){
		// Props:
		// 	_______________________________Data_________________________________
		// 	+ columns (array of objects)							| columns
		// 	+ title in head table									| title?
		// 	______________________________Methods_______________________________
		// 	+ method for getting data from API (API-func)	| getDataFromAPI
		// 	+ save method (API-func)								| saveDataToAPI
		// 	______________________________Options_______________________________
		// 	+ pagination												| optPagination
		// 	+ enableNewRow												| enableNewRow?
		super(props)

		this.state = {
			data: [], // data must correspond with column
			changes: [], // change { type: "CHANGE" | "DELETE" | "NEW_ROW", payload: { id, col?, value?, typeValue } }
			selectedRows: [], // this state for delete function
		}
	}

	editCell(oldValue, newValue, row, column){
		this.setState({ 
			changes: [
				...this.state.changes,
				{
					type: "CHANGE",
					payload: {
						id: row.id,
						col: column.dataField,
						value: newValue
					}
				}] 
		})
	}

	selectRow(row, isSelect, rowIndex, e){
		isSelect?
			this.setState({ selectedRows: [...this.state.selectedRows, row] }):
			this.setState({ selectedRows: this.state.selectedRows.filter(selRow => selRow.id !== row.id) })
	}

	selectAllRows(isSelect, rows){
		isSelect?
			this.setState({ selectedRows: rows }):
			this.setState({ selectedRows: [] })
	}

	// Actions
	newRow(){
		this.setState({
			data: [this.props.columns.reduce((prev, curr) => {
				return {...prev, [curr.dataField !== "id" && curr.dataField]: null}
			}, { id: this.state.data[0].id + 1}), ...this.state.data],
			changes: [...this.state.changes, {
				type: "NEW_ROW",
				payload: {
					id: this.state.data[0].id + 1
				}
			}]
		})
	}

	deleteRows(){
		if(window.confirm("Вы уверены? Данные данной строки будут удалены.")){
			const ids = this.state.selectedRows.reduce((prev, curr) => { return [...prev, curr.id] }, [])
			this.setState({
				data: this.state.data.filter(row => !ids.includes(row.id)),
				changes: [...this.state.changes, {
					type: "DELETE_ROW",
					payload: { ids }
				}]
			})
			NotificationManager.info("Не забудьте сохранить изменения", "Инфо", 2000)
		}
	}

	save(e){
		this.props.saveDataToAPI(this.state.changes, (res) => {
			if(!res.error){
				NotificationManager.success("Данные успешно отправлены!", "ОК", 3000)
				this.setState({ changes: [] })
			}
			else
				NotificationManager.warning("Данные не отправились", "Ошибка", 3000)
		}, err => NotificationManager.error("Ошибка на стороне сервера", "Ошибка", 3000))
	}

	componentDidMount(){
		this.props.getDataFromAPI(
			(res) => this.setState({ data: res.data.reverse() }),
			err => NotificationManager.warning("Не удалось получить данные от сервера", "Ошибка", 3000)
		)
	}

	render(){
		return(
			<div className={`universal__table_panel ${this.props.className}`}>
				{ this.props.title !== undefined && <h2 className="universal__table_panel_title">{this.props.title}</h2> }


				<BootstrapTable striped hover condensed bordered
					keyField='id'
					data={ this.state.data } 
					columns={ this.props.columns } 
					cellEdit={
						cellEditFactory({
							mode: 'dbclick',
							afterSaveCell: (oldValue, newValue, row, column) => { this.editCell(oldValue, newValue, row, column) }
						})
					}
					selectRow={{
						mode: 'checkbox',
						clickToSelect: true,
						clickToEdit: true,  // Click to edit cell also
						onSelect: (row, isSelect, rowIndex, e) => { this.selectRow(row, isSelect, rowIndex) },
						onSelectAll: (isSelect, rows) => { this.selectAllRows(isSelect, rows) }
					}}
					pagination={ this.props.optPagination && paginationFactory() } // Footer for pages and view count item in pages
					/>


				<div className="panel_bottom">
					<button className="btn btn-danger" onClick={e => this.deleteRows()}>Удалить</button>
					{
						this.props.enableNewRow !== undefined && this.props.enableNewRow && 
							<button className="btn btn-success" onClick={e => this.newRow()}>Новая строка</button>
					}
					<button className="btn btn-primary" onClick={e => this.save(e)}>Сохранить</button>
				</div>
			</div>
		)
	}
}