import React from "react"

import UniversalTable from "./UniversalTable"
import { getColors, saveColors } from "../api"



export default class SystemSetting extends React.Component{
	constructor(props){
		super(props)

		this.state = {

		}

		this.columnColors = [
			{
				text: 'ID',
				dataField: 'id',
				sort: true
			},
			{
				text: 'Цвет',
				dataField: 'color',
			}
		]
	}

	render(){
		return(
			<div className="SystemSetting">
				{/* Colors table */}
				<UniversalTable
					className="colors"
					title="Цвета"
					columns={this.columnColors}

					getDataFromAPI={getColors}
					saveDataToAPI={saveColors}

					optPagination={true}
					enableNewRow={true}
				/>
			</div>
		)
	}
}