import { USER } from "../types"

export const addUser = (login,  name, role, active) => ({
	type: USER.ADD_USER,
	payload: {
		login,
		name,
		role,
		active
	}
})
export const deleteUser = () => ({
	type: USER.DELETE_USER
})

export const addChange = (id, col, value) => ({
	type: USER.ADD_CHANGE,
	payload: {id, col, value}
})

export const save = () => ({
	type: USER.SAVE_CHANGES
})


