import React from "react"
import { connect } from "react-redux"
import { Form } from "react-bootstrap"
import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory, { textFilter, selectFilter } from 'react-bootstrap-table2-filter';
import cellEditFactory, { Type }  from 'react-bootstrap-table2-editor'
import paginationFactory from 'react-bootstrap-table2-paginator'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'

import { getControlOrders, getProdOrders } from "../api"
import { saveDataFromTables, editCell } from "../redux/actions/tables.action";

import "../styles/Tables.scss"


class Tables extends React.Component{
	constructor(props){
		super(props)

		this.state = {
			data: [],
			title: "Таблица для производства",
			tableName: "prod",
			exelTableData: [],
			year: new Date().getFullYear()
		}
		function priceFormatterColor(cell, row) {
			switch(row.status){
				case "В работе":
					return <span>
							<strong style={ { color: 'blue' } }>{ cell }</strong>
						</span>
				case "Отказ":
					return <span>
							<strong style={ { color: 'red' } }>{ cell }</strong>
						</span>
				case "Оплачен":
					return <span>
							<strong style={ { color: 'green' } }>{ cell }</strong>
						</span>
				default:
					return cell
			}
		}

		const checkNumber = (num) => {
			console.log(num)
			if(!Number(num) || isNaN(num))
				return {
					valid: false,
					message: "Введите число"
				}
			else if((""+num).length > 7)
				return {
					valid: false,
					message: "Вы ввели слишком большое число"
				}
			
			return true

		}

		this.prodColumns = [
			{
				text: '№ недели',
				dataField: 'week',
				sort: true,
				filter: textFilter(),
				validator: (newValue, row, column) => { return checkNumber(newValue) },
				headerStyle: (colum, colIndex) => { return { width: '7%'} }
			},
			{
				text: '№ заказа',
				dataField: 'id_fc',
				// editable: false, // If my client will want this
				sort: true,
				filter: textFilter(),
				validator: (newValue, row, column) => { return checkNumber(newValue) },
				headerStyle: (colum, colIndex) => { return { width: '8%'} }
			},
			{
				text: 'Название',
				dataField: 'name',
				filter: textFilter(),
				headerStyle: (colum, colIndex) => { return { width: '35%'} }
			},
			{
				text: 'Материал',
				dataField: 'material',
				// sort: true,
				filter: selectFilter({ options: {"Бук": "Бук", "Сосна": "Сосна"} }),
				headerStyle: (colum, colIndex) => { return { width: '10%'} }
			},
			{
				text: 'Цвет',
				dataField: 'color',
				filter: textFilter(),
				headerStyle: (colum, colIndex) => { return { width: '12%'} }
			},
			{
				text: 'Количество',
				dataField: 'count',
				validator: (newValue, row, column) => {checkNumber(newValue)},
				headerStyle: (colum, colIndex) => { return { width: '9%'} }
			},
			{
				text: 'Статус',
				dataField: 'status',
				formatter: priceFormatterColor,
				filter: selectFilter({ options: {"В работе": "В работе", "Отказ": "Отказ", "Оплачен": "Оплачен"} }),
				editor: {
					type: Type.SELECT,
					options: [{
					  value: 'В работе',
					  label: 'В работе'
					}, {
					  value: 'Оплачен',
					  label: 'Оплачен'
					}, {
					  value: 'Отказ',
					  label: 'Отказ'
					}]
				 }
			},
		]

		this.controlColumns = [
			{
				text: '№ нед.',
				dataField: 'week',
				sort: true,
				filter: textFilter(),
				validator: (newValue, row, column) => { return checkNumber(newValue) },
				headerStyle: (colum, colIndex) => { return { width: '8%'} }
			},
			{
				text: '№ зак.',
				dataField: 'id_fc',
				sort: true,
				filter: textFilter(),
				validator: (newValue, row, column) => {checkNumber(newValue)},
				headerStyle: (colum, colIndex) => { return { width: '8%'} }
			},
			{
				text: 'Сайт',
				dataField: 'site_name',
				filter: selectFilter({ options: {"Krovati-spalni.ru": "Krovati-spalni.ru", "мебельизсосны.рф": "мебельизсосны.рф", "Liman-murom.ru": "Liman-murom.ru"} }),
				headerStyle: (colum, colIndex) => { return { width: '15%'} },
				editor: {
					type: Type.SELECT,
					options: [{
					  value: 'Krovati-spalni.ru',
					  label: 'Krovati-spalni.ru'
					}, {
					  value: 'мебельизсосны.рф',
					  label: 'мебельизсосны.рф'
					}, {
					  value: 'Liman-murom.ru',
					  label: 'Liman-murom.ru'
					}]
				 }
			},
			{
				text: 'ФИО',
				dataField: 'client_name',
				filter: textFilter(),
				headerStyle: (colum, colIndex) => { return { width: '15%'} }
			},
			{
				text: 'Телефон',
				filter: textFilter(),
				dataField: 'client_phone',
				headerStyle: (colum, colIndex) => { return { width: '15%'} }
			},
			{
				text: 'Адрес',
				filter: textFilter(),
				dataField: 'client_address',
				headerStyle: (colum, colIndex) => { return { width: '15%'} }
			},
			{
				text: 'Стоимость',
				dataField: 'price',
				headerStyle: (colum, colIndex) => { return { width: '8%'} }
			},
			{
				text: 'Статус',
				dataField: 'status',
				formatter: priceFormatterColor,
				editor: {
					type: Type.SELECT,
					options: [{
					  value: 'В работе',
					  label: 'В работе'
					}, {
					  value: 'Оплачен',
					  label: 'Оплачен'
					}, {
					  value: 'Отказ',
					  label: 'Отказ'
					}]
				},
				headerStyle: (colum, colIndex) => { return { width: '8%'} }
			},
			{
				text: 'Отдали',
				dataField: 'paid',
				headerStyle: (colum, colIndex) => { return { width: '6%'} }
			},
			{
				text: 'Долг',
				dataField: 'debt',
				validator: (newValue, row, column) => {checkNumber(newValue)},
				headerStyle: (colum, colIndex) => { return { width: '6%'} }
				// formatter: (cell, row) => {return row.price - row.paid},
				// editable: false
			},
		]
	}
	onSelectForExelTable(row, isSelect, rowIndex){
		if(isSelect)
			this.setState({ exelTableData: [...this.state.exelTableData, row] })
		else
			this.setState({ exelTableData: [...this.state.exelTableData.filter(item => item.id !== row.id)] })
	}

	onSelectAllForExelTable(isSelect, rows){
		if(isSelect)
			rows.map(row => this.setState({ exelTableData: [...rows] }))
		else
		this.setState({ exelTableData: [] })
	}

	selectYearAndReloadData(e){
		this.setState({ year: +e.target.value })
		this.updateOrders(this.state.tableName, +e.target.value)
	}

	updateOrders(tableName, year){
		switch(tableName){
			case "prod":
				getProdOrders(year, (res) => {
					this.setState({ data: res.data.reverse() })
				}, (err) => console.error(err))
				break
			case "control":
				getControlOrders(year, (res) => {
					this.setState({ data: res.data.reverse(), title: "Таблица для отслеживания заказов" })
				}, (err) => console.error(err))
				break
			default:
				console.log("error")
		}
	}

	componentDidMount(){
		const tableName = this.props.location.pathname === "/tables/prod"? "prod": "control"
		this.updateOrders(tableName, this.state.year)
		this.setState({ tableName, title: tableName === "control"? "Таблица для отслеживания заказов": this.state.title })
	}

	render(){
		return(
			<div className="table-wrapper">
				<h1 style={{textAlign: "center", fontFamily: "RobotoBold", fontSize: "1.5em", marginBottom: "20px"}}>{this.state.title}</h1>

				{/* Select for year */}
				<Form.Select className="header-selects header-selects-year" aria-label="Выберете год" onChange={e => this.selectYearAndReloadData(e)}>
					<option value={new Date().getFullYear()}>Год</option>
					<option selected value="2021">2021 год</option>
					<option value="2022">2022 год</option>
				</Form.Select>

				<BootstrapTable striped hover condensed bordered
					keyField='id'
					data={ this.state.data } 
					columns={ this.props.location.pathname === "/tables/prod"? this.prodColumns: this.controlColumns } 
					cellEdit={
						cellEditFactory({
							mode: 'dbclick',
							afterSaveCell: (oldValue, newValue, row, column) => { this.props.editCellFromRedux(row.id, column.dataField, newValue) }
						})
					}
					filter={ filterFactory({}) }
					filterPosition="top"
					selectRow={{
						mode: 'checkbox',
						clickToSelect: true,
						clickToEdit: true,  // Click to edit cell also
						onSelect: (row, isSelect, rowIndex, e) => { this.onSelectForExelTable(row, isSelect, rowIndex) },
						onSelectAll: (isSelect, rows) => { this.onSelectAllForExelTable(isSelect, rows) }
					}}
					pagination={ paginationFactory() } // Footer for pages and view count item in pages
					/>

					{/* for normal export to exel file */}
					<table style={{display: "none"}} id="table-to-xls">
						<thead>
							<tr>
								{
									this.state.tableName == "prod" && this.prodColumns.map(thData => {
										return <th>{thData.text}</th>
									})
								}
								{
									this.state.tableName == "control" && this.controlColumns.map(thData => {
										return <th>{thData.text}</th>
									})
								}
							</tr>
						</thead>
						<tbody>
							{
								this.state.exelTableData.map(row => {
									return <tr key={row.id}>
										{
											this.state.tableName == "prod" && this.prodColumns.map(col => {
												return <th>{row[col.dataField]}</th>
											})
										}
										{
											this.state.tableName == "control" && this.controlColumns.map(col => {
												return <th>{row[col.dataField]}</th>
											})
										}
									</tr>
								})
							}
						</tbody>
					</table>
	
					{/* ---------------------- Control Panel -------------------- */}
					<p className="footer-table">
						<button className="btn btn-success" onClick={e => this.props.location.pathname === "/tables/prod"? this.props.save("/save_data/prod", "POST"): this.props.save("/save_data/control", "POST")}>Сохранить</button>
						<ReactHTMLTableToExcel
							id="test-table-xls-button"
							className={`btn btn-primary ${this.state.exelTableData.length === 0? "disabled": ""} download-table-xls-button`}
							table="table-to-xls"
							filename={"table_" + this.state.tableName}
							sheet="tablexls"
							buttonText="Сохранить в XLS"
							/>
					</p>
					
			</div>
		)
	}
}

const mapDispatchToProps = dispatch => ({
	editCellFromRedux: (row, col, value) => dispatch(editCell(row, col, value)),
	save: (method, url) => dispatch(saveDataFromTables(method, url))
 })
 
export default connect(null, mapDispatchToProps)(Tables)