import React from 'react';
import { connect } from "react-redux" 
import { FloatingLabel, Form } from "react-bootstrap"
import { FaTimes } from "react-icons/fa"

import "../styles/Order.scss"

class OrderPanel extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			// Order
			id: -1,
			name: "",
			// size: "",
			// color: "",
			// material: "",
			count: 1,
			price: 0,
			allPrice: 0

		}

		// Refs for input components
		this.refColor = React.createRef()
		this.refMaterial = React.createRef()
	}

	componentDidMount(){
		this.setState({ id: this.props.id })
	}

	// For changing fields and recalc prices
	reCalcAllPrice(current, toggler){
		switch(toggler){
			case 0:
				return this.state.price * current
			case 1:
				return current * this.state.count
			default:
				return 0
		}
	}

	onChangeName(e){ this.setState({ name: e.target.value }) }
	onChangeCount(e){ this.setState({ count: +e.target.value, allPrice: this.reCalcAllPrice(+e.target.value, 0) }) }
	onChangeMainPrice(e){ this.setState({ price: +e.target.value, allPrice: this.reCalcAllPrice(+e.target.value, 1) }) }
	

	validate(e){

	}

	render(){
		const FInput = _props => {
			return (
				<FloatingLabel
					controlId="floatingInput"
					label={_props.label}
					className="mb-12"
				>
					<Form.Control {..._props} stype={_props.type} placeholder={_props.placeholder} required/>
				</FloatingLabel>
			)
		}
		const FLabel = _props => {
			return (
				<p className={"FLabel_line" + (_props.mark? " mark_bold" : "")}>
					<span className="line_key_span">{_props._key}</span>
					<span className="line_value_span">{_props.value}</span>
				</p>
			)	
		}
		return(
			<div className="order_panel">
				{/* <h2>Заказ #{this.props.id + 1} - {this.state.name}</h2> */}

				<div className="order_panel_close" onClick={e => this.props.deleteOrderFromArrayById(this.state.id)}>
					<FaTimes />
				</div>

				<form className="oreder_panel_main_info" action="javascript:0">
					<span className="order_panel_id">#{this.props.id + 1}</span>
					<FloatingLabel style={{width: "40%"}} controlId="floatingInput" label="Наименование" className="mb-12">
						<Form.Control 
							ref={this.name} 
							type="text" 
							name="name"
							placeholder="Стул" 
							onChange={this.onChangeName.bind(this)}
							// isInvalid={this.state.validatation.includes("name")} 
							// onBlur={e => this.validate(e)} 
							minLength={3}
							required/>
					</FloatingLabel>

					<Form.Select style={{width: "12%"}} ref={this.refMaterial} aria-label="Default select example" className="order_panel_select select_material">
						<option value="-">Матер.</option>
						<option value="Сосна">Сосна</option>
						<option value="Береза">Береза</option>
						<option value="Бук">Бук</option>
						<option value="Дуб">Дуб</option>
						<option value="Ясень">Ясень</option>
					</Form.Select>

					<Form.Select style={{width: "10%"}} ref={this.refColor} aria-label="Default select example" className="order_panel_select select_color">
						<option value="-">Цвет</option>
						<option value="лак">лак</option>
						<option value="Венге-1">Венге-1</option>
						<option value="Венге-2">Венге-2</option>
						<option value="Венге-3">Венге-3</option>
					</Form.Select>



					<FloatingLabel style={{width: "8%"}} controlId="floatingInput" label="Кол" className="mb-12">
						<Form.Control min={1} max={10000000} ref={this.count} type="number" placeholder="4" defaultValue={1} onChange={this.onChangeCount.bind(this)} required/>
					</FloatingLabel>

					<FloatingLabel style={{width: "14%"}} controlId="floatingInput" label="Цена" className="mb-12">
						<Form.Control min={0} max={10000000} ref={this.price} type="number" placeholder="540" defaultValue={0} onChange={this.onChangeMainPrice.bind(this)} required/>
					</FloatingLabel>
					<FloatingLabel style={{width: "14%"}} controlId="floatingInput" label="Сумма" className="mb-12">
						<Form.Control disabled ref={this.price} type="number" placeholder="540" value={this.state.price * this.state.count} onChange={this.onChangeMainPrice.bind(this)} required/>
					</FloatingLabel>
					<button 
						className="btn btn-primary apply_order" 
						onClick={e => this.props.addOrderToArray(e, {...this.state, color: this.refColor.current.value, material: this.refMaterial.current.value})}
					>Применить
					</button>
				</form>
					
			</div>
		)
	}
	
}

export default OrderPanel