import { NotificationManager } from "react-notifications"

import { sendDataUsersForSave } from "../../api"
import { ERole, USER } from "../types"


const initialStateUser = {
	login: null,
	name: null,
	role: ERole.ADMIN,
	active: 1,
	changes: []
}

export const user = (state = initialStateUser, action) => {
	switch(action.type){
		case USER.ADD_USER:
			return {
				...action.payload,
				changes: []
			}
		case USER.DELETE_USER:
			return initialStateUser

		case USER.ADD_CHANGE:
			return {
				...state,
				changes: [...state.changes, action.payload]
			}
		case USER.SAVE_CHANGES:
			sendDataUsersForSave(state.changes, (res) => {
				if(!res.error) NotificationManager.success("Данные сохранены!", "OK", 3000)
				else NotificationManager.warning("Что-то пошло не так", "Ошибка", 3000)
			}, err => NotificationManager.warning("Кажется, ошибка на сервере", "Ошибка", 3000))
			return {
				...state,
				changes: []
			}
		default: 
			return state
	}
}