import React from "react"
import { NotificationManager } from "react-notifications"
import { Form, FloatingLabel } from "react-bootstrap"
import { login } from "../api"
import { addUser } from "../redux/actions/users.action"
import { connect } from "react-redux"
import { authUser } from "../redux/actions/app.action"

import "../styles/Forms.scss"
// import { BrowserHistory } from "react-router"

class Login extends React.Component{
	constructor(props){
		super(props)

		this.login = React.createRef()
		this.password = React.createRef()
	}
	static contextTypes = {
		router: () => true, // replace with PropTypes.object if you use them
	 }

	onLogin(e){
		e.preventDefault()
		login({
			login: this.login.current.value, 
			password: this.password.current.value
		}, data => {
			console.log(data)
			if(!data.error){
				localStorage.setItem("token", data.jwt)
				NotificationManager.success("Вы авторизованы!", "OK", 3000)
				this.props.addUser(data.login, data.name, data.role, data.active)
				this.props.authUser(true)
				this.props.history.push("/")
			}
			else NotificationManager.warning("Неверный логин или пароль", "Ошибка", 3000)
		}, err => {
			NotificationManager.warning("Ошибка на сервере(", "Ошибка", 3000)
		})
	}
	render(){
		return(
			<div className="main">
				<Form className="login-form" style={{display: "flex", flexDirection: "column", width: "400px"}}>
					<h2>Авторизация</h2>
					<FloatingLabel controlId="floatingInput" label="Логин" className="mb-12">
						<Form.Control ref={this.login} type="text" placeholder="Иванов Иван Иванович" name="login" autoFocus/>
					</FloatingLabel>
					<FloatingLabel controlId="floatingInput" label="Пароль" className="mb-12">
						<Form.Control ref={this.password} type="password" placeholder="Login" name="password"/>
					</FloatingLabel>
					<button className="btn btn-primary" onClick={this.onLogin.bind(this)}>Войти</button>
				</Form>
			</div>
		)
	}
}
const mapStateToProps = state => ({
	auth: state.app.authorization
})

const mapDispatchToProps = dispatch => ({
	addUser: (login,  name, role, active) => dispatch(addUser(login,  name, role, active)),
	authUser: (auth) => dispatch(authUser(auth))
})

export default connect(mapStateToProps, mapDispatchToProps)(Login)