import React from "react"
import { Link } from "react-router-dom"
import { getInvoices, postNewInvoiceDeliverBack, getInvoiceDOCX } from "../api"
import { Form, FloatingLabel, ButtonToolbar, ButtonGroup, Button } from "react-bootstrap"
import { FaChevronLeft, FaChevronRight } from "react-icons/fa"
import { connect } from "react-redux"
import FileDownload from "js-file-download"
import { NotificationManager } from 'react-notifications';


import "../styles/Invoices.scss"
import Invoice from "./Invoice"


class InvoicesList extends React.Component{
	constructor(props){
		super(props)

		this.state = {
			invoices: [],
			activeInvoice: false,
			activeRow: {},
			selectedYear: new Date().getFullYear(),
			selectedWeek: -1,

			deliverVisible: false,
			selectInvoices: [],

			// panigation
			countItemsInPage: 10,
			currentPage: 1,
			maxCountPages: 5
		}

		this.deliverNumberOrder = React.createRef()
	}

	updateListInvoices(cb){
		getInvoices(this.state.selectedYear, data => {
			this.setState({ invoices: data.data.reverse() })
			cb(data.data).bind(this)
		}, err => console.log(err))
	}
	componentDidMount(){
		this.updateListInvoices((data) => {
			if(Boolean(this.props.match.params.id)){
				const __row = data.filter(row => row.id === +this.props.match.params.id)
				console.log(__row)
				if(__row.length !== 0){
					this.setState({
						activeInvoice: true, 
						activeRow: __row[0],
						selectInvoices: [] 
					})
				}
				else
					this.props.history.push("/invoices")
			}
			
		})
	} 

	onEventHandlerForLabel(e, row){
		switch(e.detail){
			// One click
			case 1:

				let indexSelected = -1
				this.state.selectInvoices.map((sel, index) => {
					if(sel.id === row.id) {
						indexSelected = index
						return sel
					}
				})
				if (indexSelected !== -1) {
					let arr = this.state.selectInvoices
					arr.splice(indexSelected, 1)
					this.setState({ selectInvoices: arr })
				}
				else this.setState({ selectInvoices: [...this.state.selectInvoices, row] })
				
				break
			// Double click
			case 2:
				e.preventDefault()
				this.props.history.push(`/invoices/${row.id}`)
				// 
				break
			default:
				return
		}
	}

	selectWeekAndReloadData(e){
		this.setState({ selectedWeek: +e.target.value })
	}

	selectYearAndReloadData(e){
		getInvoices(+e.target.value, data => {
			this.setState({ invoices: data.data })
		}, err => console.log(err))
	}
	createDeliverBack(e){
		if(this.state.invoices.filter(invoice => invoice.id_fc === +this.deliverNumberOrder.current.value).length !== 0){
			postNewInvoiceDeliverBack(+this.deliverNumberOrder.current.value, (res) => {
				if(res.error) NotificationManager.warning("Не удалось создать новый довоз", "Ошибка", 3000)
				else this.setState({ 
					invoices: [
						{ ...this.state.invoices
							.filter(invoice => invoice.id_fc === +this.deliverNumberOrder.current.value)[0],
							deliver_back: 1,
							id: this.state.invoices[this.state.invoices.length - 1].id + 1
						},
						...this.state.invoices, 
					],
					deliverVisible: false
				})
			}, err => NotificationManager.warning("Не удалось создать новый довоз", "Ошибка", 3000))
		}
		else{
			NotificationManager.warning("Такого заказа не существует", "Ошибка", 3000)
		}
	}
	printSelected(e){
		getInvoiceDOCX(this.state.selectInvoices.reduce((prev, curr) => { return [...prev, curr.id] }, []), res => {
			FileDownload(res, `заказы.docx`)
		}, err => console.log(err))
	}
	

	// --------------------------------------------------------------------------
	// ----------------------------- Panigation ---------------------------------
	// --------------------------------------------------------------------------


	range(size, startAt = 0) {
		return [...Array(size).keys()].map(i => i + startAt);
	}
	toForward(e){
		this.state.currentPage < Math.ceil(this.state.invoices.length / this.state.countItemsInPage) && 
			this.setState({ currentPage: this.state.currentPage + 1 })
	}
	toBackward(e){ this.state.currentPage > 1 && this.setState({ currentPage: this.state.currentPage - 1 }) }
	
	pickPage(page){
		this.setState({ currentPage: page })
	}

	render(){

		// for selects
		const weeks = this.state.invoices.reduce((prev, curr) => { return (!(prev.includes(curr.week))? [...prev, curr.week]: prev)  }, []).sort()
		
		// ----- for arrays render including panigation ------
			

		const invoices = this.state.selectedWeek === -1?
			this.state.invoices.filter((_, index) => 
				index >= (this.state.currentPage - 1) * this.state.countItemsInPage && 
				index < (this.state.currentPage) * this.state.countItemsInPage):
			this.state.invoices.filter(invoice => invoice.week === this.state.selectedWeek).filter((_, index) => 
				index >= (this.state.currentPage - 1) * this.state.countItemsInPage && 
				index < (this.state.currentPage) * this.state.countItemsInPage
			)

		// panigation
		const countPages = this.state.selectedWeek === -1?
			Math.ceil(this.state.invoices.length / this.state.countItemsInPage):
			Math.ceil(this.state.invoices.filter(invoice => invoice.week === this.state.selectedWeek).length / this.state.countItemsInPage)
		return(
			<div className="invoices_section">
				
				<h1 className="header">
					{
						// arrow button to back list
						this.state.activeInvoice && 
							<Link to="/invoices" className="invoices_section__back"><FaChevronLeft /></Link>
					}
					Заказы 
					{this.state.activeInvoice? `: ${this.state.activeRow.id_fc}`: ""}
				</h1>

				{
					!this.state.activeInvoice && <div className="invoices_list_wrapper">

						<div className="panel">
							{/* Number of week for order */}
							<Form.Select className="header-selects header-selects-week" aria-label="Неделя" onChange={e => this.selectWeekAndReloadData(e)}>
								<option value="-1">№ недели</option>
								{
									weeks.map(week => {
										return <option key={week} value={week}>{week}</option>
									})
								}
							</Form.Select>
							{/* Number of year for created date of order */}
							<Form.Select className="header-selects header-selects-year" aria-label="Выберете год" onChange={e => this.selectYearAndReloadData(e)}>
								<option value="2021">Год</option>
								<option selected value="2021">2021 год</option>
								<option value="2022">2022 год</option>
							</Form.Select>
						</div>

						<div className="label_thead">
							<span className="number_order">№ Недели</span>
							<span className="number_order">№ заказа</span>
							<span className="client_name">ФИО</span>
							<span className="client_phone">Телефон</span>
						</div>

						<ul className="invoices_list">
							{
								invoices.map((invoice, index) => {
									return <li onClick={e => this.onEventHandlerForLabel(e, invoice)} key={`li_${index}`}>
										<Form.Check type="checkbox" id={`invoice_${index}`} className={`choice choice_${index}`} 
											label={
												<React.Fragment>
													<span className="field number_order">{invoice.week}</span>
													<span className="field number_order">{invoice.id_fc}</span>
													<span className="field client_name">{invoice.client_name}</span>
													<span className="field client_phone">{invoice.client_phone}</span>
													{!!invoice.deliver_back? <span className="deliver_back">Довоз</span>: <></>} 
												</React.Fragment>
											}
											/>
										</li>
								})
							}
						</ul>
						{
							// A new invoice -> deliver back
							this.state.deliverVisible && <div className="deliver">
								<span>Создать новый довоз: </span>
								<FloatingLabel controlId="floatingInput" label="Номер заказа" className="mb-4">
									<Form.Control type="text" placeholder="3400" autoFocus ref={this.deliverNumberOrder}/>
								</FloatingLabel>
								<div className="button-wrapper">
									<button className="btn btn-primary add-deliver" onClick={this.createDeliverBack.bind(this)}>Создать</button>
									<button className="btn btn-danger" onClick={e => this.setState({ deliverVisible: false })}>Закрыть</button>
								</div>
							</div>
						}

						<div className="panigation">
							<Form.Select onChange={e => this.setState({ countItemsInPage: +e.target.value })} className="header-selects header-selects-year" aria-label="Выберете айтомов">
								<option value="10" selected>Кол-во: 10</option>
								<option value="20">Кол-во: 20</option>
								<option value="30">Кол-во: 30</option>
								<option value="40">Кол-во: 40</option>
								<option value="50">Кол-во: 50</option>
							</Form.Select>

							<ButtonToolbar aria-label="Toolbar with button groups" className="panigation-pages">
								<ButtonGroup className="me-2" aria-label="First group">
									<Button onClick={this.toBackward.bind(this)}><FaChevronLeft /></Button>
								</ButtonGroup>
								<ButtonGroup className="me-2" aria-label="Second group">
									{
										this.range(countPages).map((_, index) => {
											return <Button onClick={e => this.pickPage(index + 1)} active={this.state.currentPage === index + 1}>{index + 1}</Button>
										})
									}
								</ButtonGroup>
								<ButtonGroup aria-label="Third group">
									<Button onClick={this.toForward.bind(this)}><FaChevronRight /></Button>
								</ButtonGroup>
							</ButtonToolbar>
						</div>



						<p className="button_wrapper">
							<button onClick={e => this.setState({ deliverVisible: true })} className="btn btn-success" style={{marginRight: "10px"}}>Сделать довоз</button>
							<button onClick={this.printSelected.bind(this)} className={`btn btn-primary ${this.state.selectInvoices.length === 0? "disabled": ""}`}>Печать</button>
						</p>
					</div>
				}

				{
					this.state.activeInvoice && <Invoice invoiceData={this.state.activeRow} />
				}
				
			</div>
		)
	}
}

const mapStateToProps = state => ({
	auth: state.app.authorization,
	user: state.user
})

export default connect(mapStateToProps, null)(InvoicesList)