import { ERole } from "./redux/types"

// Components
import Login from "./components/Login"
import Registration from "./components/Registration"
import Order from "./components/Order"
import Tables from "./components/Tables"
import Users from "./components/ManageUsers"
import Home from "./components/Home"
import InvoicesList from "./components/InvoicesList"
import SystemSettings from "./components/SystemSettings"

import {
	FaPlus,
	FaList,
	FaTable,
	FaUser,
	FaSignInAlt,
	FaCogs
} from "react-icons/fa"

export const routes = [
	{
		path: "/logout",
		name: "Выйти",
		role_user: ERole.USER,
		isNavBar: false,
		isPrivate: true,
		component: Login
	},
	{
		path: "/login",
		name: "Авторизация",
		role_user: ERole.NON,
		isNavBar: false,
		isPrivate: false,
		icon: <FaSignInAlt />,
		component: Login
	},
	{
		path: "/registration",
		name: "Регистрация",
		role_user: ERole.NON,
		isNavBar: false,
		isPrivate: false,
		icon: <FaSignInAlt />,
		component: Registration
	},


	{
		path: "/new_order",
		name: "Новый заказ",
		role_user: ERole.USER,
		isNavBar: true,
		isPrivate: true,
		icon: <FaPlus />,
		component: Order
	},
	{
		path: "/invoices",
		name: "Заказы",
		role_user: ERole.USER,
		isNavBar: true,
		isPrivate: true,
		isExact: true,
		icon: <FaList />,
		component: InvoicesList
	},
	{
		path: "/invoices/:id",
		name: "Заказы",
		role_user: ERole.USER,
		isExact: true,
		isNavBar: false,
		isPrivate: true,
		icon: <FaList />,
		component: InvoicesList
	},
	{
		path: "/tables/prod",
		name: "Таблица для производства",
		role_user: ERole.EDITOR,
		isNavBar: true,
		isPrivate: true,
		icon: <FaTable/>,
		component: Tables
	},
	{
		path: "/tables/control",
		name: "Таблица для отслеживания",
		role_user: ERole.EDITOR,
		isNavBar: true,
		isPrivate: true,
		icon: <FaTable/>,
		component: Tables
	},
	{
		path: "/users",
		name: "Управление пользователями",
		role_user: ERole.ADMIN,
		isNavBar: true,
		isPrivate: true,
		icon: <FaUser/>,
		component: Users
	},
	{
		path: "/settings",
		name: "Системные настройки",
		role_user: ERole.USER,
		isNavBar: true,
		isPrivate: true,
		icon: <FaCogs/>,
		component: SystemSettings
	},
	{
		path: "/",
		name: "Главная",
		role_user: ERole.USER,
		isNavBar: false,
		component: Home
	},
]