import React from "react"
import { connect } from "react-redux"

class Home extends React.Component{
	constructor(props){
		super(props)
	}
	getCurrentWeek(){
		//define a date object variable that will take the current system date  
		let todaydate = new Date();  

		//find the year of the current date  
		var oneJan =  new Date(todaydate.getFullYear(), 0, 1);   

		// calculating number of days in given year before a given date   
		var numberOfDays =  Math.floor((todaydate - oneJan) / (24 * 60 * 60 * 1000));   

		// adding 1 since to current date and returns value starting from 0   
		return Math.ceil(( todaydate.getDay() + 1 + numberOfDays) / 7) - 1;     
	}
	render(){
		return(
			<div className="Home">
				{
					this.props.auth && <>
						<h1>Здравствуйте, {this.props.user.name}</h1>
						<h2 className="current_week">Текущая неделя: <span className="mark">{this.getCurrentWeek()}</span></h2>
					</>
				}
				{
					!this.props.auth && <p>Пожалуйста, авторизуйтесь!</p>
				}
			</div>
		)
	}
}

const mapPropsToProps = state => ({
	user: state.user,
	auth: state.app.authorization
})

export default connect(mapPropsToProps, null)(Home)