import { NotificationManager } from "react-notifications"

import { sendDataTablesForSave } from "../../api"
import { TABLES } from "../types"

const initTablesState = {
	changes: []
}

export const tables = (state = initTablesState, action) => {
	switch(action.type){
		case TABLES.EDIT_CELL:
			return {
				...state,
				changes: [...state.changes, action.payload]
			}
		case TABLES.SAVE_DATA:
			sendDataTablesForSave(action.payload.url, state.changes, (res) => {
				if(!res.error) NotificationManager.success("Данные сохранены!", "OK", 3000)
				else NotificationManager.warning("Что-то пошло не так", "Ошибка", 3000)
			}, err => NotificationManager.warning("Кажется, ошибка на сервере", "Ошибка", 3000))
			return {
				...state,
				changes: []
			}
		default:
			return state
	}
}