import { Link } from "react-router-dom"
import { Dropdown } from "react-bootstrap"
import { FaCog, FaSignOutAlt } from 'react-icons/fa'

import { connect } from 'react-redux'
import { deleteUser } from '../redux/actions/users.action'
import { logoutUser } from '../redux/actions/app.action'

import "../styles/Header.scss"


function Header(props) {

	const logout = (e) => {
		props.logoutUser()
		props.deleteUser()
	}

	return (
		<header className="main_header">
			{
				props.auth && 
					<Dropdown>
						<Dropdown.Toggle variant="success" id="dropdown-basic">
							{props.user.login}
						</Dropdown.Toggle>

						<Dropdown.Menu>
							<Link className="dropdown-item" to="/my_settings"><FaCog style={{marginRight: "5px"}}/>Настройки</Link>
							<Link className="dropdown-item" to="/" onClick={logout}><FaSignOutAlt  style={{marginRight: "5px"}}/>Выйти</Link>
						</Dropdown.Menu>
					</Dropdown>
			}
		</header>
	)
}

const mapStateToProps = state => ({
	user: state.user,
	auth: state.app.authorization
})

const mapDispatchToProps = dispatch => ({
	logoutUser: () => dispatch(logoutUser()),
	deleteUser: () => dispatch(deleteUser())
})

export default connect(mapStateToProps, mapDispatchToProps)(Header)