import React from "react"
import { connect } from "react-redux"
import { Form } from "react-bootstrap"
import { NotificationManager } from 'react-notifications'
import BootstrapTable from 'react-bootstrap-table-next'
import cellEditFactory, { Type } from 'react-bootstrap-table2-editor'
import FileDownload from "js-file-download"

import { addInvoiceChange, saveInvoiceChanges } from "../redux/actions/app.action"
import { getInvoiceOrders, getInvoiceDOCX } from "../api"

import { HOST } from "../config.json"



class Invoice extends React.Component{
	constructor(props){
		super(props)

		this.state = {
			prods: [],
			selectedRows: []
		}

		this.columns = [
			{
				text: "№ п/п",
				dataField: "index",
				headerStyle: (colum, colIndex) => { return { width: '5%'} },
				editable: false
			},
			{
				text: "Наименование товара (описание выполненных работ, оказанных услуг)",
				dataField: "name",
				headerStyle: (colum, colIndex) => { return { width: '30%'} }
			},
			{
				text: "Материал",
				dataField: "material",
				editor: {
					type: Type.SELECT,
					options: [{
					  value: '-',
					  label: 'НЕТ'
					},{
					  value: 'Сосна',
					  label: 'Сосна'
					}, {
					  value: 'Береза',
					  label: 'Береза'
					}, {
					  value: 'Бук',
					  label: 'Бук'
					},
					{
					  value: 'Дуб',
					  label: 'Дуб'
					},
					{
					  value: 'Ясень',
					  label: 'Ясень'
					}]
				}
			},
			{
				text: "Цвет",
				dataField: "color",
				editor: {
					type: Type.SELECT,
					options: [{
						value: '-',
						label: 'НЕТ'
					},{
					  value: 'лак',
					  label: 'лак'
					}, {
					  value: 'Венге-1',
					  label: 'Венге-1'
					}, {
					  value: 'Венге-2',
					  label: 'Венге-2'
					}]
				}
			},
			{
				text: "Ед. изм.",
				dataField: "units",
				editable: false
			},
			{
				text: "Кол-во",
				dataField: "count",
				validator: (newValue, row, column) => {
					if (isNaN(newValue)) {
					  return {
						 valid: false,
						 message: 'Должно быть число'
					  };
					}
					if (newValue > 10000) {
					  return {
						 valid: false,
						 message: 'Слишком большое число!'
					  };
					}
					return true;
				 }
			},
			{
				text: "Цена (тариф) руб.коп.",
				dataField: "price"
			},
			{
				text: "Стоимость товаров (работ, услуг) руб.коп.",
				dataField: "allPrice"
			}
		]

		this.invoiceRef = React.createRef()
	}

	printInvoice(){
		let mainData = this.props.invoiceData
		getInvoiceDOCX([this.props.invoiceData.id], res => {
			FileDownload(res, `заказ_${mainData.id_fc}.docx`)
		}, err => NotificationManager.warning("Не удалось скачать документ", "Ошибка", 3000))
	}

	newRow(){
		this.setState({ prods: [...this.state.prods, {
			allPrice: 0,
			assemblyAndEntering: false,
			color: "-",
			count: 0,
			id: this.state.prods[this.state.prods.length - 1].id + 1,
			id_fc: this.props.invoiceData.id_fc,
			id_iv: this.props.invoiceData.id,
			index: this.state.prods[this.state.prods.length - 1].index + 1,
			material: "-",
			name: "",
			price: 0,
			units: "шт.",
			week: this.props.invoiceData.week
		}] })

		this.props.addInvoiceChange({
			type: "NEW_ROW",
			payload: {
				id: this.state.prods[this.state.prods.length - 1].id + 1
			}
		})
	}

	deleteRow(){
		// List of id for selected rows
		const ids = this.state.selectedRows.reduce((prev, curr) => { return [...prev, curr.id] }, [])

		// delete row in component, including prods which id don't selected
		this.setState({ prods: this.state.prods.filter(prod => !ids.includes(prod.id)) })

		// make action -> DELETE_ROW
		this.props.addInvoiceChange({ type: "DELETE_ROW", payload: ids })
	}

	// send changes and save them
	saveInvoice(){
		this.props.saveInvoiceChanges(this.props.invoiceData)
	}
	
	componentDidMount(){
		getInvoiceOrders(res => {
			let index = 1
			this.setState({ 
				prods: res.data
					.filter(order => order.id_iv === this.props.invoiceData.id)
					.reduce((prev, curr) => { return [...prev, { ...curr, units: "шт.", index: index++, assemblyAndEntering: !!curr.assembly && !!curr.entering}] }, [])
			})
		}, err => NotificationManager.warning("Не удалось загрузить данные", "Ошибка", 3000))
	}

	onSelectForExelTable(row, isSelect, rowIndex){
		isSelect?
			this.setState({ selectedRows: [...this.state.selectedRows, row] }):
			this.setState({ selectedRows: [...this.state.selectedRows.filter(item => item.id !== row.id)] })
	}

	onSelectAllForExelTable(isSelect, rows){
		isSelect?
			this.setState({ selectedRows: [...rows] }):
			this.setState({ selectedRows: [] })
	}
	updateAllPriceCell(newValue, row, column){
		console.log(newValue, row, column)
		if(column.dataField === "price"){
			const currProds = this.state.prods
			currProds.map((prod, index) => {
				if(prod.id === row.id){
					currProds[index].allPrice = Number(row.count) * Number(newValue)
					return currProds[index]
				}
			})
			this.setState({ prods: currProds })
			this.props.addInvoiceChange({ type: "CHANGE", payload: { col: "allPrice", value: Number(row.count) * Number(newValue), id: row.id } })
		}
		if(column.dataField === "count"){
			const currProds = this.state.prods
			currProds.map((prod, index) => {
				if(prod.id === row.id){
					currProds[index].allPrice = Number(row.price) * Number(newValue)
					return currProds[index]
				}
			})
			this.setState({ prods: currProds })
			this.props.addInvoiceChange({ type: "CHANGE", payload: { col: "allPrice", value: Number(row.price) * Number(newValue), id: row.id } })
		}
	}
	render(){
		const switchImage = (siteName) => {
			switch(siteName){
				case "Krovati-spalni.ru":
					return <img src={`${HOST}/images/logo_krovati_var_22.png`} alt="logo" />
					break
				case "мебельизсосны.рф":
					return <img src={`${HOST}/images/ms.png`} alt="logo" />
					break
				case "Liman-murom.ru":
					return <img src={`${HOST}/images/lim.jpg`} alt="logo" />
					break
				default:
					return <img title="no image" />

			}
		}

		const selectOption = (defaultValue, type) => {
			return <Form.Select className="" aria-label="Выберете год" onChange={e => this.props.addInvoiceChange({ type, payload: e.target.value })}>
				<option value="">Выберете</option>
				<option selected={!!defaultValue} value="Да">Да</option>
				<option selected={!defaultValue} value="Нет">Нет</option>
			</Form.Select>
		}

		const inputPrice = (defaultValue, type) => {
			const changeInput = (e) => {
				if(e.key === "Enter") this.props.addInvoiceChange({ type, payload: e.target.value })
			}
			return <input type="text" defaultValue={defaultValue} onKeyPress={e => changeInput(e)}/>
		}
		return(
			<div className="Invoice-wrapper">
				{ !!this.props.invoiceData.deliver_back && <span className="deliver-back-mark">Довоз</span> }
				<div className="Invoice" ref={this.invoiceRef}>
					{/* <Component /> */}
					<div className="header">
						<div className="logo">
							{
								switchImage(this.props.invoiceData.site_name)
							}
						</div>
						<div className="info_block">
							<div className="client_info client_name"><span>ФИО</span>: {this.props.invoiceData.client_name}</div>
							<div className="client_info client_phone"><span>Телефон</span>: {this.props.invoiceData.client_phone}</div>
							<div className="client_info client_address"><span>Адрес</span>: {this.props.invoiceData.client_address}</div>
						</div>
					</div>

					<div className="sub-header">
						<p>
							<span>Заказ № <span className="number_order">{this.props.invoiceData.id_fc}</span></span>
							<span>Дата доставки <span className="date_order">{this.props.invoiceData.date_order}</span></span>
						</p>
					</div>

					<div className="prods">
						<BootstrapTable striped hover condensed bordered
							keyField="id"
							data={ this.state.prods } 
							columns={ this.columns } 
							cellEdit={
								cellEditFactory({
									mode: 'dbclick',
									afterSaveCell: (oldValue, newValue, row, column) => { 
										this.updateAllPriceCell(newValue, row, column)
										this.props.addInvoiceChange({ type: "CHANGE", payload: { col: column.dataField, value: newValue, id: row.id } })
									 }
								})
							}
							selectRow={{
								mode: 'checkbox',
								clickToSelect: true,
								clickToEdit: true,  // Click to edit cell also
								onSelect: (row, isSelect, rowIndex, e) => { this.onSelectForExelTable(row, isSelect, rowIndex) },
								onSelectAll: (isSelect, rows) => { this.onSelectAllForExelTable(isSelect, rows) }
							}}
						/>
					</div>

					<div className="table-bottom-panel">
						<button className="btn btn-success" onClick={this.newRow.bind(this)}>Новая строка</button>
						<button className="btn btn-danger" onClick={this.deleteRow.bind(this)}>Удалить строку</button>
					</div>

					<p className="gost">
						Данная мебель соответствует ГОСТу 16371-93 и госту 19917-93, имеет гигиенический
						сертификат, сертификат соответствия, признана годной к эксплуатации.
					</p>

					<div className="assembly-and-entering">
						<span className="name-field">Доставка <span className="field field_data field-delivery">{selectOption(this.props.invoiceData.delivery, "CHANGE/delivery")} {inputPrice(this.props.invoiceData.delivery_price, "CHANGE/delivery_price")}</span></span>
						<span className="name-field">Сбор <span className="field field_data field-assembly">{selectOption(this.props.invoiceData.assembly, "CHANGE/assembly")} {inputPrice(this.props.invoiceData.assembly_price, "CHANGE/assembly_price")}</span></span>
						<span className="name-field">Занос <span className="field field_data field-entering">{selectOption(this.props.invoiceData.entering, "CHANGE/entering")} {inputPrice(this.props.invoiceData.entering_price, "CHANGE/entering_price")}</span></span>
						<span className="name-field">Сбор/Занос <span className="field field_data field-assembly-entering">{selectOption(this.props.invoiceData.assemblyAndEntering, "CHANGE/assemblyAndEntering")} {inputPrice(this.props.invoiceData.assemblyAndEntering_price, "CHANGE/assemblyAndEntering_price")}</span></span>
					</div>

					<div className="write-for-client">
						<p>Заказчик:</p>
						<p className="write-for-client-data">
							<span className="info write-name-client">ФИО<span className="field"></span></span>
							<span className="info write-phone-client">Тел.<span className="field"></span></span>
						</p>
					</div>

					<p className="signature">Подпись <span className="field"></span></p>

				</div>
				<div className="panel-bottom">
					<button className="btn btn-success" onClick={this.saveInvoice.bind(this)}>Соханить</button>
					<button className="btn btn-primary" onClick={this.printInvoice.bind(this)}>Печать</button>
				</div>
			</div>
		)
	}
}

const mapDispatchToProps = f => ({
	addInvoiceChange: (action) => f(addInvoiceChange(action)),
	saveInvoiceChanges: (invoiceData) => f(saveInvoiceChanges(invoiceData)),
}) 

export default connect(null, mapDispatchToProps)(Invoice)